    background-color: #8CAFFF;
    text-decoration:none;
    padding: 10px 20px;
    border-radius: 5px;
    font-size: large;
    font-weight: bolder;
    cursor: pointer;

  }

  .book-now-button:hover {
    background-color: #6B94D4;
    -webkit-text-decoration-style: none;
            text-decoration-style: none;
    transition: background-color 0.3s ease;

