  #mobile {
   display: none;
  }

  #mobile i {
    color: white;
    align-items: center;
  }

  #bar.fas.fa-times {
    font-size: 30px; /* Adjust the size as needed */
    margin-right: 20px; /* Adjust the right margin as needed */
  }

  #bar.fas.fa-bars {
    font-size: 30px; /* Adjust the size as needed */
    margin-right: 20px; /* Adjust the right margin as needed */
  }

  .scroll-home-button {
    position: fixed;
    bottom: 20px;
    right: 20px;
    color: rgba(255, 255, 255, 0.8);
    background-color: transparent;
    border: none;
    font-size: 2rem;
    border-radius: 20px;
    z-index: 999;
    transition: 0.3s;
  }
  
