  }

  @media screen and (max-width: 760px) {

    .navbar {
      z-index: 3;
    }

    #nav-links {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      justify-content: flex-start;
      position: fixed;
      font-size: larger;
      top: -20px;
      right: 0px;
      width: 300px;
      height: 100vh;
      background-color: #4A4A4A;
      box-shadow: 0 40px 60px rgba(0, 0, 0, 0.1);
      padding: 90px 0 0 10px; 
      transition: 0.3s;
  }

  #nav-links.active{
    right: -350px;
    transition: 0.3s;
  }

  .nav-link {
    margin-bottom: 25px;
  }

  .nav-link:hover {
    color: #8CAFFF;
    font-weight: bolder;
    text-decoration: underline;
    transition: 0.3s ease;
  }

  #mobile {
    display: block;
    z-index: 3;
  }

  #mobile i {
    font-size: 24px;
    cursor: pointer;

  }

  

